<template>
  <div class="forgot">
    <div class="nav-bg"></div>
    <van-nav-bar>
      <template #left>
        <div class="logo"></div>
        <div class="logo-title">Luckin Coffee</div>
      </template>
      <template #right>
        <div @click="goHome">
          <van-icon name="ellipsis" class="right-icon" />
        </div>
      </template>
    </van-nav-bar>

    <div class="login-form">
      <div class="login-bg"></div>
      <div class="login-box">
        <div class="tilte">找回密码</div>
        <div class="en-title">forgot password!</div>

        <van-cell-group>
          <van-field v-model="userInfo.phone" type="tel" label="手机号" placeholder="请输入用户名" />
          <van-field
            v-model="userInfo.password"
            placeholder="请输入新密码"
            label="密码"
            :type="isPassword ? 'password' : 'text'"
            :right-icon="isPassword ? 'closed-eye' : 'eye-o'"
            @click-right-icon="viewPassword"
          />
          <van-field v-model="userInfo.emill" label="邮箱" placeholder="请输入用户名" />
          <van-field
            v-model="userInfo.validCode"
            center
            clearable
            label="短信验证码"
            placeholder="请输入短信验证码"
          >
            <template #button>
              <van-button size="small" type="primary" @click="sendValidCode">{{text}}</van-button>
            </template>
          </van-field>
        </van-cell-group>
      </div>
      <div class="buttom-box">
        <div class="clearfix forgot">
          <span class="fr" @click="goLogin">已有账号，立即登录</span>
        </div>

        <div class="login-btn">
          <van-button
            round
            block
            native-type="submit"
            color="#0C34BA"
            @click="commit"
          >提&nbsp;&nbsp;交</van-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import "../assets/less/forgot.less";

import { validForm } from "../assets/js/validForm";

import { utils } from "../assets/js/utils";
export default {
  data() {
    return {
      isPassword: true,
      userInfo: {
        phone: "",
        password: "",
        emill: "",
        validCode: "",
      },
      text: "发送验证码",
      isSend: false,
    };
  },
  methods: {
    // 返回上一页
    back() {
      this.$router.go(-1);
    },
    //返回登录界面
    goLogin() {
      this.$router.push({ name: "Login" });
    },
    //返回主页
    goHome() {
      this.$router.push({ name: "Home" });
    },
    //显示密码
    viewPassword() {
      this.isPassword = !this.isPassword;
    },
    // 发送验证码
    sendValidCode() {
      if (this.userInfo.emill == "") {
        this.$toast("请输入邮箱!");
        return;
      }
      let time = 5;
      this.text = time + "S后重新发送";
      this.isSend = true;

      let timer = setInterval(() => {
        if (time == 0) {
          clearInterval(timer);
          this.isSend = false;
          this.text = "发送验证码";
        } else {
          time--;
          this.text = time + "S后重新发送";
        }
      }, 1000);

      //发起获取邮箱验证码请求
      // 验证通过

      this.$toast.loading({
        message: "加载中...",
        forbidClike: true,
        duration: 0,
        loadingType: "spinner",
      });

      // 参数序列化
      let data = utils.queryString({
        email: this.userInfo.emill,
        appkey: this.appkey,
      });

      this.axios({
        method: "POST",
        url: "/emailValidCode",
        data,
      })
        .then((result) => {
          this.$toast.clear();
          

          this.$notify({
            type: "warning",
            message: result.data.msg,
          });
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },
    //提交数据
    commit() {
      //验证修改密码表单
      let o = {
        phone: {
          value: this.userInfo.phone,
          reg: /^1[3-9]\d{9}$/,
          errorMsg: "手机号格式不正确",
        },
        password: {
          value: this.userInfo.password,
          reg: /^[A-Za-z]\w{5,15}$/,
          errorMsg: "密码支持字母数字",
        },
        email: {
          value: this.userInfo.emill,
          reg: /^\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/,
          errorMsg: "邮箱格式不正确",
        },
        validCode: {
          value: this.userInfo.validCode,
          reg: /^\d{6}$/,
          errorMsg: "验证为6位数字",
        },
      };
      if (!validForm.valid(o)) {
        // 验证不通过
        return;
      }

      // f发起忘记密码请求

      this.$toast.loading({
        message: "加载中...",
        forbidClike: true,
        duration: 0,
        loadingType: "spinner",
      });

      //参数序列化
      let data = utils.queryString({
        validCode: this.userInfo.validCode,
        appkey: this.appkey,
      });

      this.axios({
        method: "POST",
        url: "/checkValidCode",
        data,
      })
        .then((result) => {
          this.$toast.clear();
          
          if (result.data.code == "K001") {
            //验证成功
            this.undataPassword();
          } else {
            this.$notify({
              type: "warning",
              message: result.data.msg,
            });
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },
    //修改密码
    undataPassword() {
      this.$toast.loading({
        message: "加载中...",
        forbidClike: true,
        duration: 0,
        loadingType: "spinner",
      });

      // 参数序列化
      let data = utils.queryString({
        phone: this.userInfo.phone,
        password: this.userInfo.password,
        appkey: this.appkey,
      });

      this.axios({
        method: "POST",
        url: "/retrievePassword",
        data,
      })
        .then((result) => {
          
          if (result.data.code == "L001") {
            setTimeout(() => {
              this.$router.push({ name: "Login" });
              this.$toast.clear();
            },600)
          } else {
            this.$toast(result.data.msg);
          }
        })
        .catch((err) => {
          
          this.$toast.clear();
        });
    },
  },
  created() {},
};
</script>