// const { Notify } = require("vant")

// 导入
import Vue from 'vue'

// 导入插件
import { Notify } from 'vant'

// 全局注册
Vue.use(Notify)

class ValidForm {
  //验证表单
  valid(o) {
    for (const key in o) {
      if (!o[key].reg.test(o[key].value)) {
        // 提示
        Notify({
          type: 'warning',
          message: o[key].errorMsg,
        })
        // 验证失败
        return false;

      }
    }
    // 通过验证
    return true;
  }
}

// 导出
export const validForm = new ValidForm();
